import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import Home from './panels/Home';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState('home');
	const [fetchedData, setJsonZap] = useState('home');
	const [fetchedBonus, setJsonBonus] = useState('home');
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});

async function getUsersInfo() {
			const user_info = await bridge.send('VKWebAppGetUserInfo');
			var id = user_info.id;
			setUser(user_info);
			setPopout(null);

			setInterval(check_notifick, 3000, id);

			var xhr = new XMLHttpRequest();
			var url = 'https://neft-game.ru/bot/bin/api.php?key=vk-beeminiap&func=getuserinfo&userid=' + id;
			xhr.open('GET',url,true);
			xhr.send();

			xhr.onreadystatechange = function() {
  			if (xhr.readyState !== 4) {
    			return
  		}

  		if (xhr.status === 200) {
				var result = JSON.parse(xhr.responseText);
				if (result.error === "0") {
					 var error_1 = {money: "0", med: "0", rubles: "0"}
					 var error_2 = {text: "–ß—Ç–æ–±—ã –≤–æ—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ–º –Ω—É–∂–Ω–æ –ø–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–æ–µ —Å–æ–≥–ª–∞—à–µ–Ω–∏–µ —É –±–æ—Ç–∞!", time: ""}
					 setJsonZap(error_1);
					 setJsonBonus(error_2);
				} else {
					setJsonZap(result);
					getinfobonus(id);
				}
  		}

			}
}

async function check_notifick(id)  {

	var xhr = new XMLHttpRequest();
	var url = 'https://neft-game.ru/bot/bin/api.php?key=vk-beeminiap&func=notific&userid=' + id;
	xhr.open('GET',url,true);
	xhr.send();

	xhr.onreadystatechange = function() {
		if (xhr.readyState !== 4) {
			return
	}

	if (xhr.status === 200) {
		var result = JSON.parse(xhr.responseText);
		var notific = result.notific;
		if (notific === "0") {
			clearInterval(check_notifick);
			async function notific(id) {
					var result_notific = await bridge.send("VKWebAppAllowNotifications");
					if (result_notific.result === true){
						var xhr = new XMLHttpRequest();
						var url = 'https://neft-game.ru/bot/bin/api.php?key=vk-beeminiap&func=notific_ok&userid=' + id;
						xhr.open('GET',url,true);
						xhr.send();
						clearInterval(check_notifick);
					}
			}
			notific(id);
		}
	}
}
}

async function getinfobonus(id) {
		var xhr = new XMLHttpRequest();
		var url = 'https://neft-game.ru/bot/bin/api.php?key=vk-beeminiap&func=app_bonus&userid=' + id;
		xhr.open('GET',url,true);
		xhr.send();

		xhr.onreadystatechange = function() {
			if (xhr.readyState !== 4) {
				return
		}

		if (xhr.status === 200) {
			var result = JSON.parse(xhr.responseText);
			Timer(result,id);
	}
}
	}

	async function Timer(result,id) {

		var countDownDate = new Date(result.time).getTime();

		var x = setInterval(function() {

	  var now = new Date().getTime();

	  var distance = countDownDate - now;

	  var days = Math.floor(distance / (1000 * 60 * 60 * 24));
	  var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
	  var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
	  var seconds = Math.floor((distance % (1000 * 60)) / 1000);

	  var time_ = hours + " –ß–∞—Å–æ–≤ "
	  + minutes + " –ú–∏–Ω—É—Ç " + seconds + " –°–µ–∫—É–Ω–¥ ";

		var array = {time:  time_, text: "–î–æ –ë–æ–Ω—É—Å–∞ –û—Å—Ç–∞–ª–æ—Å—å: "}

		setJsonBonus(array);

	  if (distance < 0) {
	    clearInterval(x);
			var money = result.priz;
			var bonus_text = "–¢—ã –ø–æ–ª—É—á–∏–ª –ë–æ–Ω—É—Å –≤ —Ä–∞–∑–º–µ—Ä–µ üí≤"  +  money;
			var array = {time:  "", text: bonus_text}
			var xhr = new XMLHttpRequest();
			var url = 'https://neft-game.ru/bot/bin/api.php?key=vk-beeminiap&func=app_bonus_give&userid=' + id;
			xhr.open('GET',url,true);
			xhr.send();
			setJsonBonus(array);
			getUsersInfo();
	  }
	}, 1000);


	}

	getUsersInfo();
	}, []);


	function post_on_wall()  {
		bridge.send("VKWebAppShowWallPostBox", {"message": "üí∏ü§ë –Ø –∑–∞—Ä–∞–±–∞—Ç—ã–≤–∞—é –≤ BeeGame –∏ –≤—ã–≤–æ–∂—É –¥–µ–Ω—å–≥–∏ –∫—É–¥–∞ —Ö–æ—á—É!üëâ –ü–µ—Ä–µ—Ö–æ–¥–∏ –ø–æ —Å—Å—ã–ª–∫–µ –∏ –ø–æ–ª—É—á–∏ –ë–û–ù–£–°: vk.cc/ayUCkz", "attachments": "photo-197721915_457239020"});
	}


	function post_stories() {
		bridge.send("VKWebAppShowStoryBox", { "background_type" : "image", "url" : "https://neft-game.ru/storis.png", "attachment": {"text":"more" , "type": "url", "url": "https://vk.com/bee.game"}});
	}

	function buffer() {
		bridge.send("VKWebAppCopyText", {"text": "üí∏ü§ë –Ø –∑–∞—Ä–∞–±–∞—Ç—ã–≤–∞—é –≤ BeeGame –∏ –≤—ã–≤–æ–∂—É –¥–µ–Ω—å–≥–∏ –∫—É–¥–∞ —Ö–æ—á—É!üëâ –ü–µ—Ä–µ—Ö–æ–¥–∏ –ø–æ —Å—Å—ã–ª–∫–µ –∏ –ø–æ–ª—É—á–∏ –ë–û–ù–£–°: vk.cc/ayUCkz"});
	}

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} post_on_wall={post_on_wall} post_stories={post_stories} buffer={buffer} fetchedData={fetchedData} fetchedBonus={fetchedBonus} />
		</View>
	);
}

export default App;
